<template>
    <div class="lay-header horiz-container">
        <router-link to="/">
            <el-button type="text">首页</el-button>
        </router-link>
        <div class="spacer"></div>
        <el-link :underline="false" type="success" class="mx-2">
            <i class="fa fa-bell fa-lg"></i> 消息
        </el-link>
        <el-link :underline="false" type="danger" class="mx-2">
            <i class="fa fa-envelope fa-lg"></i> 邮件
        </el-link>
        <!-- 从状态栏读取当前已登录用户的信息，并显示用户名 -->
        <el-link
            :underline="false"
            type="primary"
            class="mx-2"
            v-if="currentUser"
        >当前用户： {{ currentUser }}</el-link>
        <!-- 添加注销功能 -->
        <el-link :underline="false" type="danger" class="mx-2" @click="logout">注销</el-link>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import AccountService from "../../../api/service/account_service.js"
export default {
    name: 'LayoutHeader',
    methods: {
        ...mapMutations(
            // 把 状态中定义的方法名："SET_DRAWER" 映射为当前组件的方法： setDrawer，
            ["SET_CURRENT_USER"]
        ),
        // 用户注销
        async logout() {
            console.log("logout");
            await AccountService.logOut();
            if (this.$route.path != "/") {
                this.SET_CURRENT_USER(null);    // 清空 vuex 状态中的用户信息
                localStorage.removeItem("preRoute");  // 删除已保存的前一页地址
                localStorage.removeItem("token");   // 删除已保存的用户 token
                localStorage.removeItem("currentUser");   // 删除已保存的用户
                this.$router.push("/");
            }
        },
    },

    computed: {
        // 获取当前登录的用户名
        currentUser() {
            if (localStorage.getItem('token')) {
                return localStorage.getItem('currentUser')
            }
        },
    }
}
</script>

<style>
.lay-header {
    height: 60px;
    align-items: center;
}
</style>